import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

@Component({
  selector: 'ui-layout-horizontal',
  templateUrl: './layout-horizontal.component.html',
  styleUrls: ['./layout-horizontal.component.scss'],
})
export class LayoutHorizontalComponent implements OnInit {
  showOffCanvase = false;
  isOpen = true;
  constructor(private route: ActivatedRoute, private router: Router) {}

  ngOnInit() {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        this.showOffCanvase = this.route.snapshot.data['showOffCanvase'];
      }
    });
  }
  onClick() {
    this.isOpen = !this.isOpen;
    // this.menu.isopen = !this.menu.isopen;
  }
  toggle() {
    this.showOffCanvase = !this.showOffCanvase;
  }
}
