import { Routes, RouterModule } from '@angular/router';
import { CalenderModule } from '../../../../hours-report/src/lib/calender/calender.module';



const routes: Routes = [
  {
    path: '',
    redirectTo: 'auth',
    pathMatch: 'full',
  },
  {
    path: 'auth',
    loadChildren: () =>
      import('@shlomit/auth/login').then((m) => m.LoginModule),
  },
  // {
  //   path: 'dashboard',
  //   loadChildren: () =>
  //     import('./dashboard/dashboard.module').then((m) => m.DashboardModule),
  // },
  {
    path: 'dashboard',
    loadChildren: () =>
      import('@shlomit/dashboard').then((m) => m.DashboardModule),
  },
  {
    path: 'alerts',
    loadChildren: () => import('@shlomit/alerts').then((m) => m.AlertsModule),
  },


  {
    path: 'calender',
    loadChildren: () => import('@shlomit/hours-report').then((m) => m.CalenderModule),
  },
  {
    path: 'absence',
    loadChildren: () => import('@shlomit/hours-report').then((m) => m.AbsenceModule),
  },
  {
    path: 'hours-report',
    loadChildren: () => import('@shlomit/hours-report').then((m) => m.HoursReportModule),
  },
  // {
  //   path: 'folder/:id',
  //   loadChildren: () => import('./folder/folder.module').then( m => m.FolderPageModule)
  // }
];


export const AppRoutes = RouterModule.forChild(routes);

