import { environment } from 'environments/environment';
export interface IAuth {
  persId: 'string';
  tempCode?: 'string';
}

export const AUTH = {
  LOGIN: (): string => {
    return `${environment.API_ENDPOINT.api}/api/auth/login`;
  },
  TOKEN: (): string => {
    return `${environment.API_ENDPOINT.api}/api/auth/token`;
  },
  REFRESH_TOKEN: (): string => {
    return `${environment.API_ENDPOINT.api}/api/auth/refresh-token`;
  },
  GETUSERS: (): string => {
    return `${environment.API_ENDPOINT.api}/api/auth/getusers`;
  },
};
