import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AUTH, IToken } from '@shlomit/shared/interfaces';
import { Subject } from 'rxjs';
import { isBefore } from 'date-fns';

@Injectable({ providedIn: 'root' })
export class AuthService {
  authEvents = new Subject();

  private _tokenData: IToken;

  get token(): string {
    return localStorage.getItem('access_token');
  }

  get tokenData(): IToken {
    return this._tokenData;
  }
  set tokenData(val: IToken) {
    this._tokenData = val;
    this.authEvents.next(val);
  }

  constructor(private httpClient: HttpClient) {
    this.authEvents.subscribe((data: IToken) => {
      localStorage.setItem('access_token', data.accessToken);
    });
  }

  authenticate(data) {
    this.httpClient.post(AUTH.LOGIN(), data).subscribe((data) => {});
  }
  getTokenWithSsmCode(data: { persId: string; hashMobilePhone: string }) {
    return this.httpClient.post(AUTH.TOKEN(), data);
  }

  afterLogin() {
    this.httpClient.get(AUTH.GETUSERS()).subscribe((data) => {});
  }
  logout() {
    this.tokenData = null;
  }
  hasTokenExpired(): boolean {
    let exp: number;
    const accessToken = localStorage.getItem('access_token');
    if (!accessToken) {
      return false;
    }
    try {
      exp = JSON.parse(atob(accessToken.split('.')[1])).exp;
      const tokenExpiryDate = new Date(0);
      tokenExpiryDate.setUTCSeconds(exp);
      const currentDate = new Date();
      if (isBefore(tokenExpiryDate, currentDate)) {
        // this.notifier.warn($localize`Session has expired! Please login again`);
        return true;
      }
      return false;
    } catch (error) {
      console.error(error);
      // this.notifier.error($localize`Invalid session detected! Please login`);
      return true;
    }
  }
}
